main {
  height: 100vh;
  background-color: white;
}

nav {
  padding-left: 3rem !important;
  z-index: 1;
  height: 15rem;
}

.navbar-nav {
  align-items: center;
}

.nav-link {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav-link p {
  display: none;
  opacity: 0;
}

.bg {
  font-size: 1.3rem;
  color: #ff8d8b;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  background-color: rgb(255, 255, 255);
  -webkit-filter: drop-shadow(0px 4px 6px rgba(29, 20, 20, 0.3));
  filter: drop-shadow(0px 4px 6px rgba(153, 90, 90, 0.3));
}

.bg:hover {
  color: white;
  background-color: rgb(158 126 245);
  transition: all 0.15s ease-out;
}

.active {
  color: white !important;
  font-size: 1.6rem;
  background-color: rgb(158 126 245);
  width: 5rem;
  height: 5rem;
  border-radius: 0.5rem;
  -webkit-filter: drop-shadow(0px 4px 6px rgba(29, 20, 20, 0.3));
  filter: drop-shadow(0px 4px 6px rgba(153, 90, 90, 0.3));
  transition: all 0.9s ease-out;
}

.active p {
  font-size: 1rem;
  opacity: 1;
  display: flex;
  transition: all 0.6s ease-out;
}

#loader {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 1031;
  height: 100%;
  width: 100%;
  background-color: white;
}

.spinner {
  animation: rotator 1.4s linear infinite;
}

@keyframes rotator {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(270deg);
  }
}

.path {
  stroke-dasharray: 187;
  stroke-dashoffset: 0;
  transform-origin: center;
  animation: dash 1.4s ease-in-out infinite, colors 5.6s ease-in-out infinite;
}

@keyframes colors {
  0% {
    stroke: #4285f4;
  }
  25% {
    stroke: #de3e35;
  }
  50% {
    stroke: #f7c223;
  }
  75% {
    stroke: #28cb77;
  }
  100% {
    stroke: #4285f4;
  }
}

@keyframes dash {
  0% {
    stroke-dashoffset: 187;
  }
  50% {
    stroke-dashoffset: 46.75;
    transform: rotate(135deg);
  }
  100% {
    stroke-dashoffset: 187;
    transform: rotate(450deg);
  }
}

.slides-container {
  overflow: hidden;
}

.slide {
  height: 100%;
  overflow: hidden;
}

.wave {
  width: 100%;
  height: 100%;
  overflow-y: hidden;
  position: absolute;
  top: 0;
  z-index: 0;
}

.wavet {
  width: 75%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 0;
}

.waveb {
  width: 100%;
  position: absolute;
  top: 0;
  z-index: -2;
}

.binar {
  width: 100%;
  position: absolute;
  top: 0;
  z-index: -1;
}

.intro {
  display: flex;
  justify-content: center;
  align-items: center;
}

.intro_text {
