}

.invites {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 2;
}

.intro h3,
.intro p {
  display: inline-flex;
}

.intro h3 {
  font-size: 3.5rem;
  font-family: Poppins;
  font-style: normal;
  font-weight: 400;
  color: #4a445a;
}

.intro_text h3 {
