  font-weight: 100;
  opacity: 0.1;
  font-family: 'Clicker Script', cursive;
  z-index: 0;
  right: -1.5em;
}

.rotate_text {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  z-index: 2;
}

.intro p {
  display: inline-flex;
  align-items: center;
  font-size: 2rem;
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  color: #988eb3;
}

.intro span {
  font-size: 2.8rem;
  font-family: Poppins;
  font-weight: 600;
}

.word {
  position: absolute;
  display: flex;
  opacity: 0;
}

.rotate_text p .word .letter {
  transform-origin: center center 25px;
}
.rotate_text p .word .out {
  transform: rotateX(90deg);
  transition: 0.32s cubic-bezier(0.6, 0, 0.7, 0.2);
}
.rotate_text p .word .in {
  transition: 0.38s ease;
}
.rotate_text p .word .behind {
  transform: rotateX(-90deg);
}

