}

.portfolios {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.birdscripts,
.tweetninja {
  position: relative;
  padding: 1rem 3rem;
  overflow: hidden;
  border-radius: 0.5rem;
  height: 6rem;
  width: 100%;
  transition: all 0.4s ease-out;
}

.birdscripts span,
.tweetninja span {
  position: absolute;
  background: rgba(0, 0, 0, 0.3);
  padding: 1rem;
  text-align: center;
  font-size: 24px;
  border-radius: 0.2rem;
  color: white;
  font-weight: 300;
  font-family: Poppins;
  letter-spacing: 0.2px;
  transition: all 1s ease-in-out;
  z-index: 1;
}

.birdscripts {
  background-image: linear-gradient(60deg, rgb(27, 26, 26), rgb(81, 77, 97));
  margin-right: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tweetninja {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(60deg, rgb(39, 37, 44), rgb(49, 41, 78));
}

.tweetninja img {
  height: auto;
  width: 10rem;
  transition: all 1s ease-out;
}

.birdscripts img {
  height: auto;
  width: 10rem;
  transition: all 1s ease-out;
}

.birdscripts:hover span,
.tweetninja:hover span {
  display: none;
  /* position: absolute; */
  opacity: 0;
  transition: all 1s ease-in-out;
}

.birdscripts:hover img,
.tweetninja:hover img {
  filter: brightness(130%);
  -webkit-filter: brightness(130%);
  -moz-filter: brightness(130%);
  -o-filter: brightness(130%);
  -ms-filter: brightness(130%);
  -webkit-transition: all 0.5s;
  transform: scale(1.15, 1.15);
  transition: all 1s ease-out;
}

.services {
  position: relative;
  height: 100%;
  background-color: #bca5f2;
  padding: 5rem 11rem 15rem 11rem;
