}

.wave_services {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* opacity: .75; */
  /* -ms-transform: rotate(180deg);
    transform: rotate(180deg); */
}

.services .icon-box {
  border-width: 0.15rem;
  border-color: rgba(255, 255, 255, 0.294);
  border-style: solid;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 1rem;
  border-radius: 1.5rem;
  transition: all ease-in-out 0.3s;
  background-color: hsla(0, 0%, 100%, 0.55);
  box-shadow: 0px 5px 90px 0px rgba(110, 123, 131, 0.05);
  z-index: 1;
}

.services .icon-box .icon {
  margin: 0 auto;
  width: 100px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: ease-in-out 0.3s;
  -webkit-filter: drop-shadow(0px 4px 6px rgba(29, 20, 20, 0.3));
  filter: drop-shadow(0px 4px 6px rgba(153, 90, 90, 0.3));
  position: relative;
}

.services .icon-box .icon i {
  font-size: 1.7rem;
  transition: 0.5s;
  position: relative;
}

.services .icon-box .icon svg {
  position: absolute;
  width: 75px;
  height: 75px;
}

.services .icon-box .icon svg path {
  transition: 0.5s;
  fill: #ffffff;
}

.services .icon-box h5 {
  color: #45505b;
  font-family: Poppins;
  font-weight: 500;
  margin: 0 0 15px 0;
  font-size: 1.2rem;
  transition: ease-in-out 0.3s;
}

.services .icon-box p {
  line-height: 24px;
  font-size: 0.9rem;
  margin-bottom: 0;
  font-family: Poppins;
  font-weight: 400;
  color: #886dd2;
}

.services .icon-box:hover {
  border-color: #fff;
  box-shadow: 0px 0 35px 0 rgba(0, 0, 0, 0.08);
  background-color: #fff;
}

