.contact_me {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  z-index: 1;
  background-color: white;
}

.feedback {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2rem 1rem;
  border-radius: 0.5rem;
  height: 25rem;
  width: 35rem;
  background-color: #bca5f2;
  overflow-y: auto;
  z-index: 1;
}

.feedback form label {
  color: white;
  font-family: Poppins;
  font-weight: 500;
}

.feedback form button {
  padding: 0.6rem 0;
  background-image: linear-gradient(
    60deg,
    rgb(113, 81, 255),
    rgb(137, 74, 255)
  );

  width: 100%;
  font-family: Poppins;
  font-weight: 400;
  border-color: rgba(255, 255, 255, 0.472);
  transition: all 0.9s ease-in;
}

.feedback form button:hover {
  background-image: linear-gradient(
    60deg,
    rgb(137, 74, 255),
    rgb(113, 81, 255)
  );
  border-color: transparent;
  transition: all 0.9s ease-in;
}

.feedback textarea,
.feedback input {
  background-color: rgba(255, 255, 255, 0.829);
}

.feedback form input::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  font-family: Poppins;
  font-weight: 400;
  opacity: 1; /* Firefox */
}

.feedback form input::-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  font-family: Poppins;
  font-weight: 400;
}

.feedback form input::-ms-input-placeholder {
  /* Microsoft Edge */
  font-family: Poppins;
  font-weight: 400;
}

.msg_cover {
  position: absolute;
  margin-bottom: 7rem;
  z-index: 0;
}

.mobileform {
  display: none;
}
